@import 'src/global/variables';
@import 'src/global/mixins';

.loading {
  @include flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  font-size: 24px;
}

.container {
  min-width: 310px;
  max-width: 1256px;
  font-size: 16px;
}

.nft-wrapper {
  display: inline-flex;
  width: 100%;
  justify-content: start;
  margin-bottom: 17px;
}

.inner {
  display: block;
  margin: 0 8px;
  max-width: 233.6px;
  border-radius: 5% 5% 5% 5%;
  box-shadow: 0 0 10px rgba(200, 200, 200);
  border-collapse: collapse;
  text-decoration: none;
  font-family: 'PingFang HK';

  &:first-child {
    margin-left: 12px;
  }

  &:last-child {
    margin-right: 12px;
  }
}

.nft-image {
  width: 233.6px;
  height: 224px;
  border-radius: 5% 5% 0 0;
  margin-bottom: 0;
}

.info-box {
  padding: 16px 0 16px 8px;
  margin-top: 0;
  border-radius: 0 0 10px 10px;
  color: black;
}

.price-box {
  display: flex;
  font-size: 14px;
  margin-top: 3px;
  align-items: center;
}

.text-box {
  @include lineClamp(1);
}

.eth-icon {
  max-width: 14px;
  max-height: 9px;
  margin: 0 4px;
}

.scroll-wrapper {
  overflow-x: scroll;
  padding-right: 4px;
}

.scroll-wrapper::-webkit-scrollbar {
  display: none;
}
